<template>
<div class="banner">
	<div class="banner_del">
		<div class="banner_text">
			<h3><span>Бесплатная доставка</span> по всему городу</h3>
			<p>Привезем шины на следущий день</p>
			<button-main class="button-yellow">Подробнее</button-main>
		</div>
		<img class="banner_img" src="../assets/banner-img-1.png" alt="">
	</div>
</div>
<product-filter v-on:sortFiter="selectParams" />
<product-list :items="items" />
</template>

<script>
import axios from 'axios'
import ProductList from '@/components/ProductList'
import ButtonMain from '@/components/ui/ButtonMain.vue'
import ProductFilter from '@/components/ProductFilter.vue'

export default {
	components: { ButtonMain, ProductFilter, ProductList },
	data() {
		return {
			items: [],
			params: { limit: 4, page: 1 },
			title: 'SHINPI — магазин шин и дисков'
		}
	},
	methods: {
		selectParams(par) {
			this.params = { limit: 4, page: 1 }
			this.params = { ...this.params, ...par }
			this.productsGet()
		},
		async productsGet() {
			const res = await axios.get('http://localhost:3000/api/product/', {
				params: this.params
			})
			this.items = res.data
		}
	},
	beforeMount() {
		document.title = this.title
		this.productsGet()
	}
}
</script>

