<template>
<div class="filter">
	<select-filter v-model="selectedWidth" :options="sortWidth"></select-filter>
	<select-filter v-model="selectedHeight" :options="sortHeight"></select-filter>
	<select-filter v-model="selectedDiameter" :options="sortDiameter"></select-filter>
	<select-filter v-model="selectedSeason" :options="sortSeason"></select-filter>
</div>
</template>

<script>
import SelectFilter from '@/components/ui/SelectFilter.vue'

export default {
	name: 'product-filter',
	components: {
		SelectFilter
	},
	data() {
		return {
			filter: {},
			selectedWidth: '0',
			selectedHeight: '0',
			selectedDiameter: '0',
			selectedSeason: '0',
			sortWidth: [
				{ value: '0', name: 'Ширина'},
				{ value: 165, name: '165'},
				{ value: 175, name: '175'},
				{ value: 185, name: '185'},
				{ value: 195, name: '195'},
				{ value: 205, name: '205'},
				{ value: 215, name: '215'},
				{ value: 225, name: '225'}
			],
			sortHeight: [
				{ value: '0', name: 'Профиль'},
				{ value: '25', name: '25'},
				{ value: '30', name: '30'},
				{ value: '35', name: '35'},
				{ value: '40', name: '40'},
				{ value: '45', name: '45'},
				{ value: '50', name: '50'},
				{ value: '55', name: '55'},
				{ value: '60', name: '60'},
				{ value: '65', name: '65'},
				{ value: '70', name: '70'},
				{ value: '75', name: '75'},
				{ value: '80', name: '80'},
				{ value: '85', name: '85'}
			],
			sortDiameter: [
				{ value: '0', name: 'Диаметр'},
				{ value: '12', name: '12'},
				{ value: '12C', name: 'R12C'},
				{ value: '13', name: 'R13'},
				{ value: '13C', name: 'R13C'},
				{ value: '14', name: '14'},
				{ value: '14C', name: 'R14C'},
				{ value: '15', name: 'R15'},
				{ value: '15C', name: 'R15C'},
				{ value: '16', name: 'R16'},
				{ value: '16C', name: 'R16C'},
				{ value: '17', name: 'R17'},
				{ value: '17C', name: 'R17C'},
				{ value: '18', name: 'R18'},
				{ value: '18C', name: 'R18C'}
			],
			sortSeason: [
				{ value: '0', name: 'Сезон'},
				{ value: '1', name: 'Зима'},
				{ value: '2', name: 'Лето'}
			]
		}
	},
	watch: {
		selectedWidth(newValue) {
			if (newValue === '0') {
				delete this.filter.width
				return this.$emit('sortFiter', this.filter)
			}
			this.filter.width = newValue
			this.$emit('sortFiter', this.filter)
		},
		selectedHeight(newValue) {
			if (newValue === '0') {
				delete this.filter.height
				return this.$emit('sortFiter', this.filter)
			}
			this.filter.height = newValue
			this.$emit('sortFiter', this.filter)
		},
		selectedDiameter(newValue) {
			if (newValue === '0') {
				delete this.filter.diameter
				return this.$emit('sortFiter', this.filter)
			}
			this.filter.diameter = newValue
			this.$emit('sortFiter', this.filter)
		},
		selectedSeason(newValue) {
			if (newValue === '0') {
				delete this.filter.season
				return this.$emit('sortFiter', this.filter)
			}
			this.filter.season = newValue
			this.$emit('sortFiter', this.filter)
		}
	}
}
</script>

<style>
.filter {
	width: 980px;
	border-radius: 6px;
	margin: 30px auto;
	background: #FFF;
	padding: 20px;
	display: grid;
	gap: 20px;
	grid-template-columns: 230px 230px 230px 230px;
}
</style>