<template>
	<select class="slct" v-model="modelValue" @change="changeOption">
		<option v-for="option in options" :key="option.value" :value="option.value">{{ option.name }}</option>
	</select>
</template>

<script>
export default {
	name: 'select-filter',
	props: {
		modelValue: {
			type: String
		},
		options: {
			type: Array,
			default: () => []
		}
	},
	methods: {
		changeOption(event) {
			this.$emit('update:modelValue', event.target.value);
		}
	}
}
</script>

<style>
.slct {
	padding: 0 15px;
	height: 30px;
	line-height: 30px;
	background: #f0f0f0;
	color: #1B1B1B;
	border: 0;
	border-radius: 4px;
}
</style>