<template>
<div class="product">
	<div class="product_title">
		<h1>{{ brand.name }} {{ model.name }} {{ product.width }}/{{ product.height }} {{ product.diameter }}</h1>
	</div>
	<div class="wrapper">
		<div class="product_body">
			<div class="product_img">
				<img v-if="!product.img" src="../assets/no-img.png" alt="">
			</div>
			<div class="product_info">
				<div class="product_params">
					<ul>
						<li><div><span class="icon_width"></span>{{ product.width }}</div> ширина</li>
						<li><div><span class="icon_height"></span>{{ product.height }}</div> профиль</li>
						<li><div><span class="icon_diameter"></span>{{ product.diameter }}</div> диаметр</li>
						<li><div><span class="icon_season"></span>{{ product.season }}</div> сезон</li>
						<li><div><span class="icon_weight"></span>{{ product.load_index }}</div> индекс нагрузки</li>
						<li><div><span class="icon_speed"></span>{{ product.speed_index }}</div> индекс скорости</li>
					</ul>
				</div>
				<div class="product_purchase">
					<div class="product_buy">
						<div class="buy_price">
							<span>{{ product.price.toLocaleString() }} ₽</span>
							<div class="buy_quantity">

							</div>
						</div>
						<button class="buy_button"><span>В корзину</span><i class="buy_icon"></i></button>
						<div class="buy_remains">
							<span style="color: #127700" v-if="product.quantity > 6">В наличии много</span>
							<span style="color: #770000" v-else>Осталось {{ product.quantity }}</span>
						</div>
					</div>
					<div class="product_conditions">
						<p>Оплата онлайн или при получении</p>
						<div class="conditions_delivery">Бесплатная доставка по всей России</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>
import axios from 'axios'
import ButtonMain from '@/components/ui/ButtonMain.vue'

export default {
	components: { ButtonMain },
	data() {
		return {
			product: {},
			brand: {},
			model: {}
		}
	},
	methods: {
		productGet() {
			axios.get('http://localhost:3000/api/product/' + this.$route.params.id).then(res => {
				this.product = res.data
				res.data.season === 2 ? this.product.season = 'Лето' : this.product.season = 'Зима'
				this.brand = res.data.brand
				this.model = res.data.model
			})
		}
	},
	created() {
		this.productGet()
	},
	mounted() {
		document.title = `${this.brand.name} ${this.model.name} ${this.product.width}/${this.product.height} ${this.product.diameter} — SHINPI`
	}
}
</script>

<style>
.product_title { width: 1020px; margin: 30px auto; }
.product_body { width: 1020px; margin: 0 auto; display: flex; justify-content: space-between; }
.product_img { width: 415px; height: 350px; }
.product_img img { height: 350px; }
.product_info { width: 575px; display: flex; justify-content: space-between; }
.product_params { width: 220px; padding: 30px 0; }
.product_purchase { width: 280px; padding: 30px; border: 1px solid #F0F0F0; border-radius: 6px; display: flex; flex-direction: column; justify-content: space-between; }
.product_params ul { list-style: none; }
.product_params ul li { margin: 0 0 20px 0px }
.product_params ul li div { background: #F0F0F0; padding: 5px 10px; margin: 0 10px 0 0; font-weight: 600; border-radius: 4px; display: inline-flex; }
.product_params ul li div span { margin: 0 10px 0 0; }
.buy_price { display: flex; justify-content: space-between; align-items: center; margin: 0 0 15px 0; }
.buy_price span { font-size: 32px; font-weight: 600; }
.buy_quantity { background: #F0F0F0; width: 95px; height: 30px; border-radius: 4px; }
.buy_button { padding: 0 20px; height: 36px; background: #FFBE00; color: #000; border-radius: 4px; border: 0; text-transform: uppercase; font-size: 14px; font-weight: 600; width: 100%; }
.buy_button span { height: 36px; line-height: 36px; }
i.buy_icon { border-left: 1px solid rgba(0, 0, 0, 0.3); background: url(../assets/icons/cart.svg) no-repeat right center; width: 26px; height: 36px; padding: 0 0 0 10px; margin: 0 0 0 10px; float: right; }
.conditions_delivery { background: url(../assets/icons/delivery.svg) no-repeat 90% center #ECF9E6; border-radius: 6px; margin: 15px 0 0 0; padding: 15px 70px 15px 30px; font-weight: 600; }
.buy_remains { margin: 5px 0; text-align: right; }
.buy_remains span { font-size: 10px; font-weight: 700; text-transform: uppercase; }
</style>